@tailwind base;
@tailwind components;
@tailwind utilities;

*{
     font-family: Inter,sans-serif;
     padding: 0;
     margin: 0;
}
html{
     scroll-behavior: smooth;
}
#loading-animation{
     height: 100vh;
     width: 100%;
     display: flex;
     justify-content: center;
     align-items: center;
}
/* navbar */
#logo{
     height: 80px;
     object-fit: contain;
}
.underlined{
     border-bottom: blue 1px solid;
     color: blue !important;
}
.nav-container{
     width: 100%;
     padding: 0 15%;
     background-color: white;
     height: 100px;
     display: flex;
     align-items: center;
     position: fixed;
     z-index: 5;
}
.navbar{
     display: flex;
     align-items: center;
     width: 100%;
     height: 100%;
     justify-content: space-around;
}
.nav-logo{
     width: 160px;
     height: 60px;
     margin: auto 0;
}
#callIcon{
     color: #096ad8;
     font-size: 36px;
     margin-right: 15px;
}
.nav-list{
     display: flex;
     height: 100%;
     align-items: center;
}
.nav-list-item{
     padding: 40px 15px;

}
.nav-list-item a{
     color: #333;
     font-size: 13px;
     letter-spacing: .06em;
     font-weight: bold;
     transition: 0.2s ease-in;
}
.nav-list-item a:hover{
     color: blue;
     transition: 0.5s;
}
.nav-call{
     border-left: #666666 1px solid;
     padding-left: 15px;
     font-size: 18px;
}
.nav-call p:first-child{
     color: #666666;
}


/* navbar hamburger */
.ham-container {
    /* display: inline-block; */
    display: none;
    cursor: pointer;
}
  
.bar1, .bar2, .bar3 {
    width: 35px;
    height: 5px;
    background-color: #333;
    margin: 6px 0;
    transition: 0.4s;   
}
   
.change .bar1 {
     -webkit-transform: rotate(-45deg) translate(-9px, 6px);
     transform: rotate(-45deg) translate(-9px, 6px);
   }
   
.change .bar2 {opacity: 0;}
   
.change .bar3 {
     -webkit-transform: rotate(45deg) translate(-8px, -8px);
     transform: rotate(45deg) translate(-8px, -8px);
}

/* home */
.home{
     background-color: #f3f7fd;
     overflow-y: hidden;
     overflow-x: hidden;
     height: 800px;
     width: 100%;
}
.Home-container{
     width: 1200px;
}
.content{
     color: #666666;
     font-weight: 300;
}
.Home-img{
     margin-top: 100px;
     width: 1000px;
     overflow: hidden;
     display: flex;
     justify-content: center;
     align-items: center;
}
.image{
     max-width: 100%;
     object-fit: cover;
}

.Home-mid-text{
     font-size: 24px;
     margin: 0 0 25px;
     color: #0e2b3d;
}
.Home-big-text{
     font-size: 60px;
     margin: 0 0 25px;
     color: #0e2b3d;
     display: flex;
     flex-direction: column;
     width: 100%;
     line-height: 110%;
}
.Home-big-text Strong{
     color: #086ad8;
}
.Home-small-text{
     color: #666666;
     font-size: 14px;
     margin: 0 0 15px;
     width: 100%;
}
.bot-con{
     color: #ffffff;
     background-color: #086ad8;
     margin: 20px 0 0;
     padding: 15px 40px;
     width: fit-content;
}
/* services */
.services{
     background-color: #f3f5f9;
     padding: 7% 17% 2%;
}
.services-title{
     font-size: 36px;
     font-family: sans-serif;
     text-align: left;
     color: #031b4e;
     font-weight: bold;
     padding-right: 50px;
     line-height: 120%;
}
.ServicesCompo{
     width: 90%;
     margin-bottom: 20px;
}

.serv-img{
     width: 45px;
     height: 45px;
     margin: 5px 25px 0 0;
}
.serv-title{
     font-weight: bold;
     font-size: 18px;
     margin-bottom: 5px;
}
.serv-content{
     font-size: 16px;
     color: #5b6987;
}

/* solution */
.solution{
     background-color: #ffffff;
     padding: 6% 0 1%;
}
.solution-title{
     font-size: 36px;
     font-weight: bold;
     margin: 30px auto 50px;
     color: #031b4e;

}
.solution-container{
     display: grid;
     grid-template-columns: auto auto auto auto;

}
.solutioncompo{
     width: 320px;
     padding: 100px 30px;
     border: 1px #e5e8ed solid;
     border-radius: 5px;
     margin: 15px;
     justify-content: center;

}
.solutioncompo img{
     max-width: 15%;
     margin: auto;
}
.solutioncompo h1{
     font-weight: bold;
     margin-top: 10px;
     font-size: 18px;
     color: #031b4e;
}

/* team */
#team{
     padding: 7% 0 2%;
}
.subtitle1{
     font-size: 24px;
     color: #086ad8;
     font-weight: 600;
     margin-bottom: 10px;
}
.title1{
     font-size: 36px;
     margin-bottom: 55px;
     font-weight: 600;
}
.about-content{
     display: grid;
     grid-template-columns: auto auto auto;
}
.aboutcompo{
     width: 380px;
     padding: 10px 15px;
}
.aboutcompo img{
     border-radius: 10px;
}
.about-compo-name{
     position: relative;
     top: -35px;
     margin-top: 15px;
     font-size: 18px;
     font-weight: 600;
}
.about-compo-pos{
     font-size: 14px;
     position: relative;
     top: -35px;
     font-weight: 500;
     color: blue;
}
.about-social{
     position: relative;
     top:5px;

}
.about-social-list{
     position: absolute;
     width: 45px;
     height: 0px;
     overflow-y: hidden;
     left: 80%;
     bottom: 50px;
     display: flex;
     flex-direction: column;
     justify-content: start;
     align-items: center;
     transition: 0.5s;
}
.about-social-list-toggle{
     height: 170px;
     transition: 0.5s;
}
.about-social-list a{
     width: 40px;
     height: 40px;
     margin: 5px 0;
}
.about-x{
     width: 45px;
     height: 45px;
     z-index: 4;
     top: -20px;
     border-radius: 10px;
     transform: rotate(45deg);
     position: relative;
     left: 80%;
     background-color: white;
}
.about-bars{
     height: 100%;
     width: 100%;
     display: flex;
     justify-content: center;
     align-items: center;
     transform: rotate(45deg);
     cursor: pointer;
     /* transition: 1s; */
     animation-name: rotationBackward;
     animation-duration: 1s;
}
.about-bars-rotation{
     animation-name: rotationForward;
     animation-duration: 1s linear;
     transform: rotate(90deg);
}
@keyframes rotationForward {
     0%   {transform: rotate(45deg);}
     100% {transform: rotate(360deg);}
   }
@keyframes rotationBackward {
     0% {transform: rotate(360deg);}
     100%   {transform: rotate(585deg);}
   }

/* blog */
#blog{
     padding: 7% 10px 1%;
}
.program-title{
     font-size: 36px;
}
.pro-container{
     width: fit-content;
     margin: 5% auto;
     display: grid;
     grid-template-columns: auto auto auto auto;
}
.pro-compo{
     width: 350px;
     height: 530px;
     margin: 15px;
     box-shadow: 1px 1px 5px #888888;
     display: flex;
     flex-direction: column;
     color: #666666;
}
.pro-compo img{
     margin: 0;
}
.pro-compo-content{
     padding: 20px 30px;
     display: flex;
     flex-direction: column;
}
.pro-title{
     font-size: 20px;
     margin: 15px 0;
     color: #0e2b3d;
     font-weight: 600;
}
.pro-meta-info{
     display: flex;
}
.pro-meta-info-item{
     font-size: 12px;
     display: flex;
     align-items: center;
     margin-right: 25px;
}
.pro-meta-info-item span{
     color: #086ad8;
     margin: 0 5px 1px 0;
}
.pro-info{
     font-size: 14px;
}
.register-but{
     margin: 30px 0;
     font-size: 12px;
     font-weight: 800;
}



input:focus, textarea:focus, select:focus{
     outline: none;
 }
/* footer */
.Footer{
     @apply flex items-center mx-10 my-5 ;
     border-top: 1px solid rgba(8,11,45,0.2);
     margin: 10px 19%;
}
.Footer-1{
     display: flex;
     align-items: center;
}
.Footer-logo{
     height: 40px;
     width: 40px;
     margin-right: 15px;
     margin-bottom: 5px;
}
.Footer-text p{
     font-size: 14px;
}


/* media query */
@media only screen and (max-width: 1350px){
     .nav-container{
          padding: 0 5%;
     }
     /* services */
     .services{
          background-color: #f3f5f9;
          padding: 11% 10% 3%;
     }
     /* solution */
     .solution-container{
          grid-template-columns: auto auto auto;
     }
     #team{
          padding: 8% 0 1%;
     }
     #blog{
          padding: 8% 0 1%;
     }
     /* footer */
     .Footer{
          margin: 10px 5%;
     }
}
@media only screen and (max-width: 1160px){
     .about-content{
          display: grid;
          grid-template-columns: auto auto;
     }
}
@media only screen and (max-width: 1050px){
     /* navbar */
     #logo{
          height: 50px;
     }
     .nav-container{
          height: fit-content;
          padding: 0;
     }
     .navbar{
          display: flex;
          flex-direction: column;
          height: fit-content;
     }
     .ham-container{
          display: inline-block;
     }
     .nav-list{
          display: none;
          width: 100%;
          padding: 15px 0;
     }
     .nav-list-item{
          border-bottom: 1px solid rgba(8,11,45,0.2);
          padding: 20px;
          width: 100%;
     }
     .nav-contact{
          display: none;
     }
     .nav-logo{
          width: 90%;
          justify-content: space-between;
     }
     .navChange{
          display: flex !important;
          flex-direction: column;
     }

     /* home */
     .home{
          height: fit-content;
          width: 100%;
     }
     .Home-img{
          display: none;
     }
     .Home-container{
          width: 100%;
          display: flex;
          flex-direction: column;
          padding: 15% 10% 10%;

     }
     .Home-content{
          width: 700px;
          margin: auto;
     }
     

     /* services */
     .services{
          padding: 5% 10%;
          flex-direction: column;
     }
     .services-title{
          margin:50px 0;
          padding-right: 150px;
     }
     .ServicesCompo{
          width: 100%;
     }

     /* solution */
     .solution-container{
          grid-template-columns: auto auto;
     }
     /* about */


     /* program */
     .pro-container{
          grid-template-columns: auto auto ;
          padding: 0;
     }
}
@media only screen and (max-width: 770px) 
{
     /* home */
     .home{
          height: fit-content;
     }
     .Home-container{
          flex-direction: column;
          padding: 15% 5% 10%;
     }
     .Home-content{
          text-align: center;
          width: 100%;
          justify-content: center;
     }
     .Home-big-text{
          font-size: 36px;
     }
     .bot-con{
          margin: 50px auto;
     }
     .Home-img{
          display: block;
          width: 100%;
          padding-bottom: 0px;
          margin-top: 0;
     }

     /* services */
     .services{
          padding: 1% 5%;
     }
     .services-title{
          text-align: center;
          margin:50px 0;
          padding-right: 0px;
          font-size: 28px;
     }

     /* solution */
     .solution-container{
          grid-template-columns: auto;
     }
     /* about */
     .about-content{
          display: grid;
          grid-template-columns: auto;
     }
     /* program */
     .pro-container{
          grid-template-columns: auto ;
     }

     /* footer */
     .Footer-logo{
          margin-right: 5px;
     }
}
@media only screen and (max-width: 400px){
     /* home */
     .Home-container{
          padding: 25% 5% 10%;
     }
     .Home-mid-text{
          font-size: 20px;
     }
     .Home-big-text{
          font-size: 24px;
          font-weight: bolder;
     }
     .Home-small-text{
          font-size: 12px;
     }
     /* service */
     .services-title{
          font-size: 24px;
     }
     .serv-title{
          font-size: 16px;
     }
     .serv-content{
          font-size: 14px;
     }
     /* solution */
     .solution-title{
          font-size: 26px;
     }
     .solutioncompo{
          width: 90%;
     }
     /* about */
     .aboutcompo{
          width: 90%;
          margin: auto;
     }
     .about-social-list{
          width: 35px;    
     }
     .title1{
          font-size: 28px;
     }
     .subtitle1{
          font-size: 20px;
     }
     .about-x{
          width: 35px;
          height: 35px;
     }
     .about-compo-name{
          font-size: 16px;
     }

     /* program */
     .pro-compo{
          width: 90%;
          height: auto;
          margin: auto;
          margin-bottom: 25px;
     }
     .pro-title{
          font-size: 16px;
     }
     .pro-info{
          font-size: 12px;
     }
     .pro-meta-info{
          flex-direction: column;
     }
     .pro-compo-content{
          padding: 10px 20px 0;

     }
     .register-but{
          margin: 20px 0;
     }
}
